.page-header {
	background: $red;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-content: flex-start;
	padding: 0 6rem 20rem;
	margin-bottom: -15rem;

	h1 {
		width: 100%;
		@extend .title-xlarge;
	}
	h2 {
		width: 100%;
		@extend .title-small;
		font-family: $body-font;
		font-weight: 300;
	}

	p {
		max-width: 75%;
	}

	&.align-bottom {
		align-content: flex-end;
	}
}

.page-header__content {
	.spilt & {
		@include column(8);
	}
}

.page-header__nav {
	display: flex;
	align-content: flex-end;
	flex-wrap: wrap;
	@include column(3.5);
}

.page-header__nav-link {
	margin-bottom: 1.5rem;
	width: 100%;
	font-weight: 300;

	&:last-child {
		margin-bottom: 0;
	}

	&::before, &::after {
		display: none;
	}

	.pretitle {
		// font-size: 2rem;
		display: block;
	}

	.title {
		font-size: 2.25rem;
		color: #fff;
	}
}












